// export default Edit;

import AddTask from "./AddTask";
import React, { useState } from 'react';
import '../index.css';

const Edit = ({ key, task, category, date, data, setData,state }) => {
  const handleDone = (key) => {
    const itemDone = data.find(item => item.key === key);
    if (itemDone) {
      itemDone.completed = true;
      const remainingItems = data.filter(item => item.key !== key);
      const updatedData = [...remainingItems, itemDone];
      localStorage.setItem('data', JSON.stringify(updatedData));
      setData(updatedData);
    }
  };

  return (
    <div className="container">
      <div className="items">
        {data.filter(item => item.task).map((item, index) => (
          <div
            key={item.key}
            className={`square ${item.completed ? 'completed' : ''}`}
          >
            <div className="edp">
            <p>{item.task}</p>
            <p>{item.category}</p>
            <p>{item.date}</p>
            <p>{item.state}</p>
            </div>
            <div className="buttons">
              <button className="btn" onClick={() => handleDone(item.key)}>Done</button>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default Edit;
